@tailwind base;
@tailwind components;
@tailwind utilities;

.btn {
  background: linear-gradient(
    92.23deg,
    #1b3a56 21.43%,
    #00536b 50.63%,
    #008086 100%,
    #1BAE89 117.04%
  );
  box-shadow: 0px 4.42184px 107.23px rgba(54, 125, 141, 0.51);
}

.btn:hover {
  background: linear-gradient(
    92.23deg,
    #008086 21.43%,
    #00536b 50.63%,
    #1BAE89 100%,
    #1b3a56 117.04%
  );
}


.active {
  color: #fff;
  border-radius: 100%;
}

@layer base {
  body {
    @apply font-secondary text-lg leading-8 text-white;
  }
  .h2 {
    @apply font-primary text-[32px] mb-6 tracking-[10%] uppercase;
  }
  .h3 {
    @apply font-primary font-semibold text-[26px] mb-6 leading-[46px];
  }
  .btn {
    @apply rounded-full font-primary text-white font-medium;
  }
  .btn-sm {
    @apply h-[48px] px-6 text-sm;
  }
  .btn-lg {
    @apply h-[56px] px-10 text-base;
  }
  .btn-link {
    @apply text-base;
  }
  .text-gradient {
    @apply bg-gradient-to-r text-transparent bg-clip-text from-[#42A6E3] to-[#1BAE89] font-bold font-primary hover:from-[#1BAE89] hover:to-[#42A6E3];
  }
  .section {
    @apply py-8 lg:py-24 lg:h-screen flex items-center;
  }
}
